import React from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";

function Movies() {
  return (
    <Container>
      <h4>Recommended for You</h4>
      <Content>
        <Wrap>
          <Link to="/detail">
            <img alt="" src="https://blog.kakaocdn.net/dn/bSnAxt/btqDGgD3wMx/s9ExsDd33hqHm0TNh27gb0/img.jpg" />
          </Link>
        </Wrap>
        <Wrap>
          <Link to="/detail">
            <img alt="" src="https://t1.daumcdn.net/thumb/R720x0/?fname=http://t1.daumcdn.net/brunch/service/user/5iog/image/-bdZRhFgzNGXXpQpSEu7Rt9KY-E.png" />
          </Link>
        </Wrap>
        <Wrap>
          <Link to="/detail">
            <img alt="" src="https://t1.daumcdn.net/thumb/R720x0/?fname=http://t1.daumcdn.net/brunch/service/user/5iog/image/-bdZRhFgzNGXXpQpSEu7Rt9KY-E.png" />
          </Link>
        </Wrap>
        <Wrap>
          <Link to="/detail">
            <img alt="" src="https://an2-img.amz.wtchn.net/image/v2/8ed35916954f01e45614b0bb98e2404b.jpg?" />
          </Link>
        </Wrap>
      </Content>
    </Container>
  );
}

export default Movies;

const Container = styled.div``;

const Content = styled.div`
  display: grid;
  grid-gap: 25px;
  grid-template-columns: repeat(4, minmax(0, 1fr));
`;

const Wrap = styled.div`
  cursor: pointer;
  border-radius: 10px;
  overflow: hidden;
  height: 80%;
  border: 3px solid rgba(249, 249, 249, 0.1);
  box-shadow: rgb(0 0 0 / 69%) 0px 26px 30px -10px,
    rgb(0 0 0 / 73%) 0px 16px 10px -10px;
  transition: all 250ms cubic-bezier(0.25, 0.46, 0.45, 0.94) 0s;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &:hover {
    transform: scale(1.05);
    border-color: rgba(249, 249, 249, 0.8);
    box-shadow: rgb(0 0 0 / 69%) 0px 26px 30px -10px,
      rgb(0 0 0 / 73%) 0px 16px 10px -10px;
  }
`;
